function rmFeatsWithNonRelSeqHash(addedFeatures){
  return new Promise(function(resolve, reject){

    if (addedFeatures.length == 0){
      // console.log('Resolving before The idx to rm calculated')
      resolve()
    }
    
    let idxToRm = [] 

    for (let i =0; i < addedFeatures.length; i++){
      if (addedFeatures[i].hasOwnProperty('SeqHash') && addedFeatures[i].SeqHash != ''){
        let isFound = false
        for (let j =0; j< AQUARIA.panel3d.attributes.sequences.length; j++){
          if (addedFeatures[i].SeqHash == AQUARIA.panel3d.attributes.sequences[j].uniprot_hash){
            isFound = true 
          }
        }
        if (isFound == false){
          idxToRm.push(i)
          console.log('The idx to rm is getting added?')
        }
      }
      if (i == addedFeatures.length - 1){
        console.log("The idx to rm are " + idxToRm)
        // reverse sort 
        if (idxToRm.length == 0){
          resolve()
        }
        idxToRm.reverse()
        for (let k =0; k < idxToRm.length; k++){
          let track = document.getElementById('track_' + addedFeatures[idxToRm[k]].Category)
          
          console.log("Track to remove is "); 
          console.log(track)

          // addedFeatures.splice(idxToRm[k], 1)
          if (k == idxToRm.length - 1){
            resolve()
          }
        }
         
      }
    }
  })
}

